import _axios from '../axios/axios-config'
import Vue from 'vue'

/**
 *  axios 
 * @param query 
 * @returns {*}
 * @author ths
 */

const fetchData = query => {
  return _axios({
    url: query.url, // 
    method: query.method || 'POST', //  POST
    params: query.params, // 
    responseType: query.responseType || 'json', // json
    auth: query.auth || { username: localStorage.getItem('access_token') },
    data: query.data || '' //  post
  })
}

Vue.prototype.$fetchData = fetchData

export default Vue
