import Axios from 'axios'
import Vue from 'vue'
import { Notify } from 'quasar'

/**
 * axios 
 */

const axios = Axios.create({
  // baseURL: Vue.prototype.$baseURL, // 
  timeout: Vue.prototype.$timeOut // 
})

// 
axios.interceptors.request.use(
  config => {
    const token = sessionStorage.getItem('access_token')
    if (token && config.type) {
      config.headers.Authorization = 'Bearer ' + token
      switch (config.type) {
        case 'FORM-DATA':
          config.transformRequest = [data => { return 'args=' + JSON.stringify(data) }]
          break
        default:
          config.headers = {
            'Content-Type': 'application/json'
          }
      }
    }
    return config
  },
  error => {
    return Promise.reject(error)
  }
)

// 
axios.interceptors.response.use(
  response => {
    return response
  },
  error => {
    const defaultNotify = {
      message: '',
      icon: 'warning',
      color: 'warning',
      position: 'top',
      timeout: 1500
    }
    switch (error.response.status) {
      case 403:
        defaultNotify.message = '(403)'
        Notify.create(defaultNotify)
        break
      case 404:
        defaultNotify.message = '(404)'
        Notify.create(defaultNotify)
        break
      case 408:
        defaultNotify.message = '(404)'
        Notify.create(defaultNotify)
        break
      case 500:
        defaultNotify.message = '(500)'
        Notify.create(defaultNotify)
        break
      case 501:
        defaultNotify.message = '(501)'
        Notify.create(defaultNotify)
        break
      case 502:
        defaultNotify.message = '(502)'
        Notify.create(defaultNotify)
        break
      case 503:
        defaultNotify.message = '(503)'
        Notify.create(defaultNotify)
        break
      case 504:
        defaultNotify.message = '(504)'
        Notify.create(defaultNotify)
        break
      case 505:
        defaultNotify.message = 'HTTP(505)'
        Notify.create(defaultNotify)
        break
    }
    if (error.code === 'ECONNABORTED' && error.message.indexOf('timeout') !== -1) {
      defaultNotify.message = ''
      Notify.create(defaultNotify)
    }
    return Promise.reject(error)
  }
)

export default axios
